import React from "react";
import { Button, Spinner } from "react-bootstrap";
import { toast } from 'react-toastify';
import FloatingTextInput from "views/mobileView/components/atoms/FloatingTextInput";
import { emailValidator } from "../../../../../libs/validators";
import AdminServices from "../../../../../services/api/AdminServices";
import AuthHeader from "../../../components/molecules/Headers/AuthHeader";

export default function EmailScreen(props) {
	const [email, setEmail] = React.useState('');
	const [nextbtnPressed, setnextbtnPressed] = React.useState(false);
	const [loading, setloading] = React.useState(true);

	const getData = async () => {
		var aa = await localStorage.getItem("lastEmail")
		console.log("check", aa)
		await setEmail(aa !== null ? aa : '');
	}

	React.useEffect(
		() => {
			getData().then(() => {
				setloading(false);
			})
		}, []);

	const onNextBtnPressed = async () => {
		setnextbtnPressed(true);
		try {
			const res = await AdminServices.emailExist(email).then((res) => res);
			if (res.type) {
				if (res.data.ClientId !== '') {
					setnextbtnPressed(false);
					localStorage.setItem("lastEmail",email)
					props.history.push({
						pathname: '/login',
						state: { ...res.data, email } // your data array of objects
					})
				} else {
					setnextbtnPressed(false);
					localStorage.setItem("lastEmail", email)
					props.history.push({
						pathname: '/signup',
						state: { email } // your data array of objects
					})
				}
			} else {
				setnextbtnPressed(false);
				toast.error("Try again ...");
			}
		} catch (error) {
			setnextbtnPressed(false);
			toast.error("Try again ...");
		}
	};



	if (loading) {
		return (
			<div className="center-screen">
				<Spinner
					as="span"
					animation="grow"
					size="sm"
					role="status"
					aria-hidden="true"
				/>
			</div>
		);
	} else {
		return (
			<div className="container">
				<AuthHeader screenType="Home" />
				<h3 className="textHeadauth ">Login/Sign up</h3>
				<div className="floating-form">
					<FloatingTextInput
						type="email"
						label="Email"
						autoFocus
						onChange={(p) => setEmail(p.target.value)}
						value={email}
					/>
				</div>
				<div className="footer">
					<Button style={{
						opacity: emailValidator(email).isValid ? 1 : 0.4,
					}}
						onClick={
							emailValidator(email).isValid ? (
								() => {
									onNextBtnPressed();
								}
							) : (
								() => { }
							)
						} variant="primary" className="btn-footer">
						Next
					</Button>

				</div>
			</div>
		);
	}
}
